
@media only screen and (max-width: 599px){
#stg-pstr {
  width: 100% !important;
}
}
#stg-desc{
  word-break: break-word;
}
.desc{
  word-break: break-all;
}
.fl-l{
  float: left;
  margin-top: 10px;
}
.fl-r{
  float: right;
}
